import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import {
	AdaptivityProvider,
	//   ConfigProvider,
	//   useAdaptivity,
	AppRoot,
	SplitLayout,
	SplitCol,
	//ViewWidth,
	View,
	Panel,
	PanelHeader,
	Header,
	Group,
	Button,
	//SimpleCell,
	Div,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

var buttons = []
for (let i = 0; i < 8; i++) {
	buttons[i] = false;
}

function btn0() {
	buttons[0] = !buttons[0]
}
function btn1() {
	buttons[1] = !buttons[1]
}
function btn2() {
	buttons[2] = !buttons[2]
}
function btn3() {
	buttons[3] = !buttons[3]
}
function btn4() {
	buttons[4] = !buttons[4]
}
function btn5() {
	buttons[5] = !buttons[5]
}
function btn6() {
	buttons[6] = !buttons[6]
}
function btn7() {
	buttons[7] = !buttons[7]
}



var curbtn = 0
setInterval(() => {
	bridge
		.send("VKWebAppFlashSetLevel", { "level": buttons[curbtn] })
		.then(data => {
			// Обработка события в случае успеха
			console.log(data.result)
			curbtn++
			if (curbtn > 7) curbtn = 0
		})
		.catch(error => {
			console.log("Мне так плёха(")
			// Обработка события в случае ошибки
		});
}, 1000);



const App = () => {
	//const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);


	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
		}
		fetchData();
	}, []);



	return (
		<AdaptivityProvider>
			<AppRoot>
				<SplitLayout header={<PanelHeader separator={false} />}>
					<SplitCol>
						<View activePanel="main">
							<Panel id="main">
								<PanelHeader>Фонарик</PanelHeader>
								<Group header={<Header mode="secondary">Растягивание по ширине</Header>}>
									<Div style={{ display: 'flex' }}>
										<Button size="s" stretched mode="primary" onClick={this.btn0}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn1}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn2}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn3}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn4}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn5}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn6}>Жми</Button>
										<Button size="s" stretched mode="primary" onClick={this.btn7}>Жми</Button>
									</Div>
								</Group>
							</Panel>
						</View>
					</SplitCol>
				</SplitLayout>
			</AppRoot >
		</AdaptivityProvider >
	);
}

export default App;
